import React, { useState, useMemo } from 'react';
import { Calendar as BigCalendar, dateFnsLocalizer } from 'react-big-calendar';
import format from 'date-fns/format';
import parse from 'date-fns/parse';
import startOfWeek from 'date-fns/startOfWeek';
import getDay from 'date-fns/getDay';
import enUS from 'date-fns/locale/en-US';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { motion } from 'framer-motion';
import EventModal from '../components/calendar/EventModal';

const locales = {
  'en-US': enUS,
};

const localizer = dateFnsLocalizer({
  format,
  parse,
  startOfWeek,
  getDay,
  locales,
});

// Event types with their corresponding styles
const eventTypes = {
  meeting: {
    className: 'bg-blue-500 hover:bg-blue-600',
    icon: '🤝',
  },
  task: {
    className: 'bg-green-500 hover:bg-green-600',
    icon: '✓',
  },
  reminder: {
    className: 'bg-yellow-500 hover:bg-yellow-600',
    icon: '⏰',
  },
  deadline: {
    className: 'bg-red-500 hover:bg-red-600',
    icon: '⚠️',
  },
};

// Sample initial events
const initialEvents = [
  {
    id: 1,
    title: 'Team Meeting',
    start: new Date(2024, 2, 15, 10, 0),
    end: new Date(2024, 2, 15, 11, 30),
    type: 'meeting',
    description: 'Weekly team sync',
    location: 'Conference Room A',
  },
  {
    id: 2,
    title: 'Project Deadline',
    start: new Date(2024, 2, 20),
    end: new Date(2024, 2, 20),
    type: 'deadline',
    description: 'Submit final project deliverables',
    location: 'Online',
  },
];

const Calendar = () => {
  const [events, setEvents] = useState(initialEvents);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [selectedSlot, setSelectedSlot] = useState(null);

  // Custom event styling
  const eventStyleGetter = (event) => {
    const style = {
      className: `${eventTypes[event.type].className} text-white rounded-lg p-1`,
      style: {
        border: 'none',
      },
    };
    return style;
  };

  // Custom toolbar to match dashboard theme
  const CustomToolbar = (toolbar) => {
    const goToBack = () => {
      toolbar.onNavigate('PREV');
    };

    const goToNext = () => {
      toolbar.onNavigate('NEXT');
    };

    const goToCurrent = () => {
      toolbar.onNavigate('TODAY');
    };

    return (
      <div className="flex justify-between items-center mb-4 p-2">
        <div className="flex space-x-2">
          <button
            onClick={goToBack}
            className="px-3 py-1 bg-gray-100 dark:bg-gray-700 rounded-md hover:bg-gray-200 dark:hover:bg-gray-600"
          >
            ←
          </button>
          <button
            onClick={goToNext}
            className="px-3 py-1 bg-gray-100 dark:bg-gray-700 rounded-md hover:bg-gray-200 dark:hover:bg-gray-600"
          >
            →
          </button>
          <button
            onClick={goToCurrent}
            className="px-3 py-1 bg-blue-500 text-white rounded-md hover:bg-blue-600"
          >
            Today
          </button>
        </div>
        <span className="text-lg font-semibold text-gray-700 dark:text-gray-300">
          {toolbar.label}
        </span>
        <div className="flex space-x-2">
          {Object.entries(eventTypes).map(([type, { icon }]) => (
            <div
              key={type}
              className="flex items-center text-sm text-gray-600 dark:text-gray-400"
            >
              <span className="mr-1">{icon}</span>
              {type.charAt(0).toUpperCase() + type.slice(1)}
            </div>
          ))}
        </div>
      </div>
    );
  };

  const handleSelectSlot = (slotInfo) => {
    setSelectedSlot(slotInfo);
    setSelectedEvent(null);
    setIsModalOpen(true);
  };

  const handleSelectEvent = (event) => {
    setSelectedEvent(event);
    setSelectedSlot(null);
    setIsModalOpen(true);
  };

  const handleSaveEvent = (eventData) => {
    if (selectedEvent) {
      // Update existing event
      setEvents(events.map(event =>
        event.id === selectedEvent.id ? { ...eventData, id: event.id } : event
      ));
    } else {
      // Create new event
      setEvents([...events, { ...eventData, id: Date.now() }]);
    }
    setIsModalOpen(false);
    setSelectedEvent(null);
    setSelectedSlot(null);
  };

  const handleDeleteEvent = (eventId) => {
    setEvents(events.filter(event => event.id !== eventId));
    setIsModalOpen(false);
    setSelectedEvent(null);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="p-6"
    >
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-6">Calendar</h1>
        
        <div className="h-[700px]">
          <BigCalendar
            localizer={localizer}
            events={events}
            startAccessor="start"
            endAccessor="end"
            selectable
            onSelectSlot={handleSelectSlot}
            onSelectEvent={handleSelectEvent}
            eventPropGetter={eventStyleGetter}
            components={{
              toolbar: CustomToolbar,
            }}
            className="dark:text-white"
          />
        </div>
      </div>

      <EventModal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedEvent(null);
          setSelectedSlot(null);
        }}
        onSave={handleSaveEvent}
        onDelete={handleDeleteEvent}
        event={selectedEvent}
        slot={selectedSlot}
        eventTypes={eventTypes}
      />
    </motion.div>
  );
};

export default Calendar; 